@use "global" as *;

/*  ------------------------------ */
.p-scroll {
  position: fixed;
  z-index: 99;
  right: rem(20);
  bottom: rem(40);
  opacity: 1;
  visibility: visible;
  transition: 0.3s ease-in-out;
  @include mq(md) {
    bottom: rem(60);
    right: rem(40);
  }
}

.p-scroll.is-active {
  opacity: 0;
  visibility: hidden;
}

.p-scroll__link {
  overflow: hidden;
  display: flex;
  align-items: flex-start; /*   */
  gap: rem(8);
  position: relative;
  height: rem(24); /*   */
}

.p-scroll__text {
  color: #fff;
  font-family: $second-font-family;
  font-size: rem(14);
  text-transform: capitalize;
  font-weight: 400;
  display: flex;
  align-items: center;
  gap: rem(8);
}

/*  ------------------------------ */
/*  */
.p-scroll__circle {
  background: rgba(#fff, 0.5);
  width: rem(16);
  height: rem(16);
  border-radius: 9999px;
  display: inline-block;
  z-index: 1;
  transform-origin: center;
  will-change: transform, opacity;
  animation: scrollDropScale 1.8s cubic-bezier(0.37, 0, 0.63, 1) infinite;
}

/*    */
/*       scale  0.75  1  0.75  */
@keyframes scrollDropScale {
  /*  */
  0% {
    transform: translateY(rem(-6)) scale(0.3);
    opacity: 0;
  }
  12% {
    opacity: 1;
  } /*  */

  /*  */
  50% {
    transform: translateY(rem(8)) scale(0.75);
    opacity: 1;
  }

  /*  */
  88% {
    transform: translateY(rem(16)) scale(0.3);
    opacity: 0.9;
  }
  100% {
    transform: translateY(rem(20)) scale(0.2);
    opacity: 0;
  }
}

/* 
   - .p-scroll__link  height  
   - keyframes  translateY(rem(...))  
*/

/*  */
@media (prefers-reduced-motion: reduce) {
  .p-scroll__circle {
    animation: none;
  }
}
