@use "global" as *;

// 
.p-top-image {
  --marquee-duration: 24s; // 
  overflow: hidden; // 
  position: relative;
  z-index: 10;
}

// 
.p-top-image__track {
  display: flex;
  width: max-content; // 
  animation: marquee var(--marquee-duration) linear infinite;
}

// 1
.p-top-image__img {
  flex: 0 0 100vw; // 
  min-width: 100vw;
  scroll-snap-align: none; // 
}

.p-top-image__img img {
  aspect-ratio: 480 / 270;
  object-fit: cover;
  width: 100%;
  height: auto;
}

// 12-50%
@keyframes marquee {
  from {
    transform: translateX(0);
  }
  to {
    transform: translateX(-50%);
  }
}

// 
@media (prefers-reduced-motion: reduce) {
  .p-top-image__track {
    animation: none;
  }
}

// PC3
@include mq(md) {
  .p-top-image {
    overflow: visible; // PC
  }
  .p-top-image__track {
    animation: none;
    transform: none;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    width: 100%;
  }
  .p-top-image__img {
    flex: initial;
    min-width: initial;
  }
  // PC3
  .p-top-image__img.is-dup {
    display: none;
  }
}
