@use "sass:math";

////////////////////
// 
////////////////////
// 
$inner: 1190px;
// 

// 
$padding-pc: 25px;
$padding-sp: 20px;
// PCPC25px

////////////////////
// 
////////////////////

$base-font-family: "Zen Kaku Gothic New", sans-serif;
$second-font-family: "Outfit", sans-serif;

// 

body {
  font-family: $base-font-family;
}

// 
$regular: 300;
$normal: 400;
$bold: 700;

////////////////////
// 
////////////////////

//
$black: #222426;
$bg-gray: #f2f2f2;
$white: #fff;
$blue: #4fc3e0;
$barge: #f4eee8;
$yellow: #ffd02b;

////////////////////
// 
////////////////////

//
$breakpoints: (
  sm: 600,
  md: 768,
  lg: 1250,
  xl: 1440,
);

//
$mediaquerys: (
  sm: "screen and (min-width: #{map-get($breakpoints,'sm')}px)",
  md: "screen and (min-width: #{map-get($breakpoints,'md')}px)",
  lg: "screen and (min-width: #{map-get($breakpoints,'lg')}px)",
  xl: "screen and (min-width: #{map-get($breakpoints,'xl')}px)",
);

// @include mq(){}md
@mixin mq($mediaquery: md) {
  @media #{map-get($mediaquerys, $mediaquery)} {
    @content;
  }
}

// rem16px10px1610
@function rem($pixels) {
  @return math.div($pixels, 16) * 1rem;
}

////////////////////
// 
////////////////////

//768px
.u-desktop {
  display: none;
  @include mq("md") {
    display: block;
  }
}

//
.u-mobile {
  @include mq("md") {
    display: none;
  }
}
body.drawer-open {
  overflow: hidden;
}
body {
  overflow-x: clip;
}
